            vm.nuoc_di = [vm.listNuocDi.find(e => e.TEN === res.di_den_text)]
            console.log(vm.listNuocDi)
            console.log(vm.nuoc_di)
            for (let [key, value] of Object.entries(genObj)) {
                vm[key] = value
            }
